import uniqid from 'uniqid'

class List {
  constructor () {
    this.items = []
  }

  addItem (count, unit, ingredient) {
    const item = {
      id: uniqid(),
      count,
      unit,
      ingredient
	}
	this.items.push(item)
	return item
  }

  deleteItem (id) {
    const index = this.items.findIndex(el => el.id === id)
    // Difference with slice and splice
    // [2,4,8] splice(1, 1) -> return 4, original array is [2,8]
    // [2,4,8] splice(1, 2) -> return [4,8], original array is [2]
    // [2,4,8] slice(1, 1) -> return nothing, original array is [2,4,8]
    // [2,4,8] slice(1, 2) -> return 4, original array is [2,4,8]
    this.items.splice(index, 1)
  }

  updateCount (id, newCount) {
    this.items.find(el => el.id === id).count = newCount
  }
}

export default List
